<script>
import {mapState} from 'vuex'
import Tx from 'ethereumjs-tx'
export default {
  data () {
    return {
      isActive: true,
      betValue: 100,
      outline: [
        ['', '', false], ['', '', false], ['', '', false], ['', '', false], ['', '', false]
      ],
      gameKindDescription: 'Make your prediction!',
      gameKind: '',
      gameKindID: [false, false, false, false, false]
    }
  },
  computed: mapState({
    coinbase: state => state.web3.coinbase,
    gas: state => state.web3.gasContractCall,
    betKey: state => state.web3.betKey,
    minBetValue: state => state.web3.ECBminBetValueNice,
    minBetFeeValue: state => state.web3.ECBminBetFeeValueNice,
    maxBetFeeValue: state => state.web3.ECBmaxBetFeeValueNice,
    ECBcontractGTAbalance: state => state.web3.BetGTAbalance,
    ECBcontractGTAbalanceNice: state => {
      if (state.web3.web3Instance !== null) {
        let _ECBcontractGTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.BetGTAbalance, 'ether')
        let _dotposition = _ECBcontractGTAbalanceEther.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_ECBcontractGTAbalanceEther.substring(0, _dotposition))
        } else {
          return parseInt(_ECBcontractGTAbalanceEther)
        }
      }
    },
    UpperStake: state => state.web3.UpperStake,
    UpperStakeNice: state => {
      if (state.web3.web3Instance !== null) {
        let _UpperStakeNice = state.web3.web3Instance().fromWei(state.web3.UpperStake, 'ether')
        let _dotposition = _UpperStakeNice.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_UpperStakeNice.substring(0, _dotposition))
        } else {
          return parseInt(_UpperStakeNice)
        }
      }
    },
    LowerStake: state => state.web3.LowerStake,
    LowerStakeNice: state => {
      if (state.web3.web3Instance !== null) {
        let _LowerStakeNice = state.web3.web3Instance().fromWei(state.web3.LowerStake, 'ether')
        let _dotposition = _LowerStakeNice.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_LowerStakeNice.substring(0, _dotposition))
        } else {
          return parseInt(_LowerStakeNice)
        }
      }
    },
    UpperAllStakes: state => state.web3.UpperAllStakes,
    UpperYourStakeShare: state => {
      return ((state.web3.UpperAllStakes === 0 ? 0 : state.web3.UpperStake / state.web3.UpperAllStakes * 100)).toString().substr(0, 6)
    },
    UpperYouWin: state => {
      if (state.web3.web3Instance !== null) {
        let _LowerAllStakesNice = parseInt(state.web3.web3Instance().fromWei(state.web3.LowerAllStakes, 'ether'))
        let _UpperStakeNice = parseInt(state.web3.web3Instance().fromWei(state.web3.UpperStake, 'ether'))
        let _UpperAllStakesNice = parseInt(state.web3.web3Instance().fromWei(state.web3.UpperAllStakes, 'ether'))
        return ((_UpperAllStakesNice === 0 ? 0 : (_UpperStakeNice / _UpperAllStakesNice) * _LowerAllStakesNice + _UpperStakeNice)).toString().substr(0, 9)
      }
    },
    UpperAllStakesNice: state => {
      if (state.web3.web3Instance !== null) {
        let _UpperAllStakesNice = state.web3.web3Instance().fromWei(state.web3.UpperAllStakes, 'ether')
        let _dotposition = _UpperAllStakesNice.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_UpperAllStakesNice.substring(0, _dotposition))
        } else {
          return parseInt(_UpperAllStakesNice)
        }
      }
    },
    LowerAllStakes: state => state.web3.LowerAllStakes,
    LowerAllStakesNice: state => {
      if (state.web3.web3Instance !== null) {
        let _LowerAllStakesNice = state.web3.web3Instance().fromWei(state.web3.LowerAllStakes, 'ether')
        let _dotposition = _LowerAllStakesNice.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_LowerAllStakesNice.substring(0, _dotposition))
        } else {
          return parseInt(_LowerAllStakesNice)
        }
      }
    },
    LowerYourStakeShare: state => {
      return ((state.web3.LowerAllStakes === 0 ? 0 : state.web3.LowerStake / state.web3.LowerAllStakes * 100)).toString().substr(0, 6)
    },
    LowerYouWin: state => {
      if (state.web3.web3Instance !== null) {
        let _UpperAllStakesNice = parseInt(state.web3.web3Instance().fromWei(state.web3.UpperAllStakes, 'ether'))
        let _LowerStakeNice = parseInt(state.web3.web3Instance().fromWei(state.web3.LowerStake, 'ether'))
        let _LowerAllStakesNice = parseInt(state.web3.web3Instance().fromWei(state.web3.LowerAllStakes, 'ether'))
        return ((_LowerAllStakesNice === 0 ? 0 : (_LowerStakeNice / _LowerAllStakesNice) * _UpperAllStakesNice + _LowerStakeNice)).toString().substr(0, 9)
      }
    },
    gasPrice: state => state.web3.gasPrice,
    betState: state => state.web3.keyPairTimeFrameList[state.web3.keyPairTimeFrameListSelected].state
  }),
  methods: {
    clickRESET11 () {
      this.outline.forEach(function (currentValue) { currentValue[0] = ''; currentValue[1] = '' })
      for (let i = 0; i <= 4; i++) {
        this.outline[i][2] = false
      }
      this.getGameKind()
    },
    setMissedPredictions (gameKind) {
      for (let i = gameKind; i >= 0; i--) {
        if (this.outline[i][0] === '' && this.outline[i][1] === '') {
          this.outline[i][0] = true
          this.outline[i][2] = true
        }
      }
    },
    getGameKind () {
      this.gameKindDescription = ''
      this.gameKindID = [false, false, false, false, false]
      if (this.outline[4][0] || this.outline[4][1]) {
        this.gameKindDescription = 'Make your prediction! Your reward will be X32 to your stake'
        this.gameKind = '32'
        this.gameKindID[4] = true
        this.outline[4][2] = true
      } else {
        if (this.outline[3][0] || this.outline[3][1]) {
          this.gameKindDescription = 'Make your prediction! Your reward will be X16 to your stake'
          this.gameKind = '16'
          this.gameKindID[3] = true
          this.outline[3][2] = true
        } else {
          if (this.outline[2][0] || this.outline[2][1]) {
            this.gameKindDescription = 'Make your prediction! Your reward will be X4 to your stake'
            this.gameKind = '4'
            this.gameKindID[2] = true
            this.outline[2][2] = true
          } else {
            if (this.outline[1][0] || this.outline[1][1]) {
              this.gameKindDescription = 'Make your prediction! Your reward will be X2 to your stake'
              this.gameKind = '2'
              this.gameKindID[1] = true
              this.outline[1][2] = true
            } else {
              if (this.outline[0][0] || this.outline[0][1]) {
                this.gameKindDescription = "Make your prediction for the future price bar against another player! Winners will share looser's bets"
                this.gameKind = '1'
                this.gameKindID[0] = true
                this.outline[0][2] = true
              } else {
                this.gameKindDescription = 'Make your prediction!'
                this.gameKind = ''
                this.gameKindID = [false, false, false, false, false]
              }
            }
          }
        }
      }
    },
    clickU11BTN () {
      this.outline[0][0] = true
      this.outline[0][1] = false
      this.getGameKind()
    },
    clickD11BTN () {
      this.outline[0][0] = false
      this.outline[0][1] = true
      this.getGameKind()
    },
    clickU2BTN () {
      this.outline[1][0] = true
      this.outline[1][1] = false
      this.setMissedPredictions(1)
      this.getGameKind()
    },
    clickD2BTN () {
      this.outline[1][0] = false
      this.outline[1][1] = true
      this.setMissedPredictions(1)
      this.getGameKind()
    },
    clickU4BTN () {
      this.outline[2][0] = true
      this.outline[2][1] = false
      this.setMissedPredictions(2)
      this.getGameKind()
    },
    clickD4BTN () {
      this.outline[2][0] = false
      this.outline[2][1] = true
      this.setMissedPredictions(2)
      this.getGameKind()
    },
    clickU16BTN () {
      this.outline[3][0] = true
      this.outline[3][1] = false
      this.setMissedPredictions(3)
      this.getGameKind()
    },
    clickD16BTN () {
      this.outline[3][0] = false
      this.outline[3][1] = true
      this.setMissedPredictions(3)
      this.getGameKind()
    },
    clickU32BTN () {
      this.outline[4][0] = true
      this.outline[4][1] = false
      this.setMissedPredictions(4)
      this.getGameKind()
    },
    clickD32BTN () {
      this.outline[4][0] = false
      this.outline[4][1] = true
      this.setMissedPredictions(4)
      this.getGameKind()
    },
    clickPlus10 (event) { this.plus(10) },
    clickPlus50 (event) { this.plus(50) },
    clickMinus10 (event) { this.minus(10) },
    clickPlus200 (event) { this.plus(200) },
    clickMinus50 (event) { this.minus(50) },
    clickMinus200 (event) { this.minus(200) },
    minus (value) {
      if (this.betValue <= value) {
        this.betValue = 0
      } else {
        this.betValue = this.betValue - value
      }
    },
    plus (value) {
      if (this.betValue + value >= this.ECBcontractGTAbalanceNice) {
        this.betValue = this.ECBcontractGTAbalanceNice
      } else {
        this.betValue = this.betValue + value
      }
    },
    isCanStake () {
      if (this.betValue < this.minBetValue) {
        console.log('Stake value < min stake bet value:', this.betValue, '<', this.minBetValue)
        return false
      }
      if (parseInt(this.betValue) + this.minBetFeeValue > this.ECBcontractGTAbalanceNice) {
        console.log('betValue + minBetFeeValue =', parseInt(this.betValue), '+', this.minBetFeeValue, parseInt(this.betValue) + this.minBetFeeValue, ' > ECBcontractGTAbalanceNice', this.ECBcontractGTAbalanceNice)
        return false
      }
      return true
    },
    sendTX (_from, _to, _value, _data, _type, _gta, _eth) {
      let _count = this.$store.state.web3.web3Instance().eth.getTransactionCount(_from)
      let rawTransaction = {
        'from': _from,
        'nonce': this.$store.state.web3.web3Instance().toHex(_count),
        'gasPrice': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasPrice),
        'gasLimit': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasContractCall),
        'to': _to,
        'value': _value,
        'data': _data
      }
      let privateKey
      if (this.$store.state.web3.private_input.indexOf('0x', 0) >= 0) {
        privateKey = this.$store.state.web3.private_input.substring(2)
      } else {
        privateKey = this.$store.state.web3.private_input
      }
      let privKey = Buffer.from(privateKey, 'hex')
      let tx = new Tx(rawTransaction)
      tx.sign(privKey)
      let serializedTx = tx.serialize()
      console.log('serializedTx ||||', serializedTx)

      let _payload
      try {
        let _res = this.$store.state.web3.web3Instance().eth.sendRawTransaction('0x' + serializedTx.toString('hex'))
        if (_type !== undefined) {
          console.log('_res', _res)
          _payload = [_res, _type, _gta, _eth]
          this.$store.dispatch('writeTransaction', _payload)
        }
      } catch (err) {
        if (_type !== undefined) {
          _payload = [err.message, _type, '', '']
          this.$store.dispatch('writeTransaction', _payload)
        }
      }
      /* let err, hash
      this.$store.state.web3.web3Instance().eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          console.log('Sent to:' + _to + ' Transaction hash: ' + hash)
        } else {
          console.log('Not sent to:' + _to + ' Error in sending: ' + err)
        }
      })
      return (err, hash) */
    },
    clickBET (event) {
      if (!this.isCanStake()) { return }
      if (this.gameKind !== '1') { return }
      if (!this.outline[0][0] && !this.outline[0][1]) { return }
      if (this.outline[0][0]) {
        this.clickCALL(event)
      } else {
        this.clickPUT(event)
      }
    },
    clickCALL (event) {
      if (!this.isCanStake()) { return }
      this.sendTX(
        this.coinbase,
        this.$store.state.ECBcontractInstance().address,
        '0x0',
        this.$store.state.ECBcontractInstance().MakeBetUpper.getData(
          this.betKey,
          this.$store.state.web3.web3Instance().toWei(this.betValue, 'ether'),
          this.$store.state.web3.web3Instance().toWei(this.minBetFeeValue, 'ether'),
          { from: this.coinbase, gasPrice: this.$store.state.web3.web3Instance().toWei(this.gasPrice, 'wei'), gas: this.gas }
        ),
        'CALL',
        this.betValue,
        ''
      )
      this.$store.dispatch('getNewBalance')
    },
    clickPUT (event) {
      if (!this.isCanStake()) { return }
      this.sendTX(
        this.coinbase,
        this.$store.state.ECBcontractInstance().address,
        '0x0',
        this.$store.state.ECBcontractInstance().MakeBetLower.getData(
          this.betKey,
          this.$store.state.web3.web3Instance().toWei(this.betValue, 'ether'),
          this.$store.state.web3.web3Instance().toWei(this.minBetFeeValue, 'ether'),
          { from: this.coinbase, gasPrice: this.$store.state.web3.web3Instance().toWei(this.gasPrice, 'wei'), gas: this.gas }
        ),
        'PUT',
        this.betValue,
        ''
      )
      this.$store.dispatch('getNewBalance')
      /* console.log('result', result, 'PUT stake=', this.$store.state.web3.web3Instance().toWei(this.betValue, 'ether'),
        'fee', this.$store.state.web3.web3Instance().toWei(0, 'ether'),
        'gasPrice=', this.$store.state.web3.web3Instance().toWei(this.gasPrice, 'wei')) */
    }
  }
}
</script>

<style scoped>
.main {
  min-height: 500px;
  max-height: 600px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.wrapper{
  position: relative;
  order-style:solid;
  border-width: 1px;
  border-color: red;
}
.betclosed {
  position: absolute;
  background-color: rgba(94, 94, 94, 0.8);
  font-size: x-large;
  color: white;
  top:0;
  left:0;
  width:100%;
  height:100%;
  text-align: center;
}
.betclosed span {
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%, -50%);
  transform:translate(-50%, -50%);
}
input {  width: 300px;}
.smallbtn {
  height: 40px; width: 40px;
  -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
}
.bet-up {
  -webkit-clip-path: polygon(50% 0%, 100% 20%, 100% 100%, 0 100%, 0 20%); /* https://bennettfeely.com/clippy/ */
  clip-path:         polygon(50% 0%, 100% 20%, 100% 100%, 0 100%, 0 20%);
}
.bet-down {
  -webkit-clip-path: polygon(100% 0, 100% 80%, 50% 100%, 0% 80%, 0 0);
  clip-path:         polygon(100% 0, 100% 80%, 50% 100%, 0% 80%, 0 0);
}
.active{
  background-color: #6D4C41;
}
.notactive{
  background-color: #4E342E;
}
.activemul{
  color: #69F0AE;/* #FFD600; */
  font-size: 1.1em;
}
.notactivemul{
  color:#8D6E63;
}
/*.v-content__wrap{
  background-color: red;
} */
</style>



