<template>
  <div class="main">
    <v-card flat color="brown lighten-1">
      <v-card-text class="px-1 py-1">
        <v-layout row align-center class="caption font-weight-thin white--text text--darken-1">
          <v-flex xs3 class="px-0 py-2 text-right" style="font-size: 0.8em;">
            <b>For betting</b>
          </v-flex>
          <v-flex xs3 class="px-0 py-2 text-left" style="font-size: 0.9em;">
            <b>{{'   '+ECBcontractGTAbalanceNice+' BIN' }}</b>
          </v-flex>
          <v-flex xs3 class="px-0 py-2 text-left" style="font-size: 0.8em;">
            Your wallet
          </v-flex>
          <v-flex xs3 class="px-0 py-2 text-right" style="font-size: 0.9em;">
            <b>{{ GTAbalanceNice +' BIN'}}</b>
          </v-flex>
          <v-flex xs2 style="font-size: 0.8em;">
            <b v-on:click="canShow = !canShow"><v-chip small color="brown lighten-2" style="font-size: 0.3em;"><v-icon dark>{{(canShow ? 'expand_less' : 'more_vert') }}</v-icon></v-chip></b>
          </v-flex>
        </v-layout>
      </v-card-text>
    </v-card>
    <v-tabs v-if="canShow" height=30 centered color="brown lighten-4">
    <v-tabs-slider color="deep-orange accent-3"></v-tabs-slider>
    <v-tab href="#tab-1" style="font-size: 0.6em;">Deposite</v-tab>
    <v-tab href="#tab-2" style="font-size: 0.6em;">Withdraw</v-tab>
    <v-tab-item id="tab-1">
      <v-card flat color="brown lighten-5">
        <v-card-text  class="px-1 py-1">
          <v-layout row class="--text text--darken-1" style="font-size: 0.6em;">You have {{ GTAbalanceNice }} BIN tokens at your personal wallet {{ coinbase }} </v-layout>
          <v-layout row class="--text text--darken-1" style="font-size: 0.6em;">For making bet you need to deposite BIN tokens to the EtherCall contract wallet</v-layout>
          <p></p>
          <v-layout row class="px-1 py-1">
            <v-flex xs6 class="px-1 py-1">
              <v-text-field v-model="valueToDepo" class="text--darken-1" prefix="" name="input-3" style="font-size: 0.8em;" label="BIN tokens to" color="brown darken-1" mask="#########"></v-text-field>
            </v-flex>
            <v-flex xs6 class="px-1 py-1">
              <v-btn dark color="brown darken-1" v-on:click="clickAccountDeposite" style="font-size: 0.6em;">Deposite</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
    </v-tab-item>
    <v-tab-item id="tab-2">
      <v-card flat color="brown lighten-5">
        <v-card-text class="px-1 py-1">
          <v-layout row class="--text text--darken-1" style="font-size: 0.6em;">You have {{ ECBcontractGTAbalanceNice }} BIN tokens at EtherCall contract wallet.</v-layout>
          <v-layout row class="--text text--darken-1" style="font-size: 0.6em;">You can withdraw it to your personal wallet {{ coinbase }}</v-layout>
          <p></p>
          <v-layout row class="px-1 py-1">
            <v-flex xs6 class="px-1 py-1">
              <v-text-field v-model="valueToWithdraw" class="text--darken-1" name="input-3" style="font-size: 0.8em;" label="BIN tokens to" color="brown darken-1" mask="#########" prefix="" ></v-text-field>
            </v-flex>
            <v-flex xs6 class="px-1 py-1">
              <v-btn dark color="brown darken-1" v-on:click="clickAccountWithdraw" style="font-size: 0.6em;">Withdraw</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
    </v-tab-item>
  </v-tabs>
  </div>
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
import Tx from 'ethereumjs-tx'
export default {
  data () {
    return {
      input_account: null,
      new_balance: null,
      valueToDepo: 0,
      valueToWithdraw: 0,
      canShow: false,
      ShowText: 'show',
      HideText: 'hide'
    }
  },
  computed: mapState({
    isInjected: state => state.web3.isInjected,
    network: state => NETWORKS[state.web3.networkId],
    coinbase: state => state.web3.coinbase,
    // private_key: '0xc3fc6952133c80dce2373a4687c3fa6cb9f87d97519cf06cb22eb2b31d59f9',
    GTAbalance: state => state.web3.GTAbalance,
    GTAbalanceNice: state => {
      if (state.web3.web3Instance !== null) {
        let _GTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.GTAbalance, 'ether')
        let _dotposition = _GTAbalanceEther.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_GTAbalanceEther.substring(0, _dotposition))
        } else {
          return parseInt(_GTAbalanceEther)
        }
      }
    },
    ECBcontractGTAbalance: state => state.web3.BetGTAbalance,
    ECBcontractGTAbalanceNice: state => {
      if (state.web3.web3Instance !== null) {
        let _ECBcontractGTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.BetGTAbalance, 'ether')
        let _dotposition = _ECBcontractGTAbalanceEther.indexOf('.')
        if (_dotposition > 0) {
          return parseInt(_ECBcontractGTAbalanceEther.substring(0, _dotposition))
        } else {
          return parseInt(_ECBcontractGTAbalanceEther)
        }
      }
    },
    GTAtoECBallowance: state => state.web3.GTAtoECBallowance
  }),
  methods: {
    transactionReceiptAsync_ (txHash, resolve, reject) {
      this.$store.state.web3.web3Instance().eth.getTransactionReceipt(txHash, (error, receipt) => {
        if (error) {
          console.log('error')// reject(error);
        } else if (receipt == null) {
          // console.log('receipt == null')
          setTimeout(
            () => this.transactionReceiptAsync_(txHash, resolve, reject),
            5000)
        } else {
          resolve() // console.log(receipt)// resolve(receipt);
          console.log('it is OK, tx ', txHash, ' mined!')
        }
      })
    },
    sendApproveDepositAtomic (_from, _to, _countTokensApproved, _type) {
      let _storeWeb3 = this.$store.state.web3
      // let _this = this
      let _count = _storeWeb3.web3Instance().eth.getTransactionCount(_from)
      let _state = this.$store.state
      let rawTransaction = {
        'from': _from,
        'nonce': _storeWeb3.web3Instance().toHex(_count),
        'gasPrice': _storeWeb3.web3Instance().toHex(_storeWeb3.gasPrice),
        'gasLimit': _storeWeb3.web3Instance().toHex(_storeWeb3.gasContractCall),
        'to': _to,
        'value': '0x0',
        'data': (_type === 'approve'
          ? _state.GTAcontractInstance().approve.getData(_state.ECBcontractInstance().address, _countTokensApproved, { from: _from })
          : _state.ECBcontractInstance().depositToken.getData(_countTokensApproved, { from: _from }))
      }
      let privateKey
      if (_storeWeb3.private_input.indexOf('0x', 0) >= 0) {
        privateKey = _storeWeb3.private_input.substring(2)
      } else {
        privateKey = _storeWeb3.private_input
      }
      let privKey = Buffer.from(privateKey, 'hex')
      let tx = new Tx(rawTransaction)
      tx.sign(privKey)
      let serializedTx = tx.serialize()
      return '0x' + serializedTx.toString('hex')
    },
    sendApproveDeposit (_from, _to, _toDepo, _countTokensToDeposit, tokensToDepositNormal) {
      let _store = this.$store
      let _storeWeb3 = this.$store.state.web3
      let _this = this
      _storeWeb3.web3Instance().eth.sendRawTransaction(_this.sendApproveDepositAtomic(_from, _to, 0, 'approve'), function (err, hash) {
        if (!err) {
          console.log('start waiting first approve', hash)
          _this.transactionReceiptAsync_(
            hash,
            function () {
              console.log('starting deposit !!!!')
              _storeWeb3.web3Instance().eth.sendRawTransaction(_this.sendApproveDepositAtomic(_from, _to, 100000000000000000000000000000, 'approve'), function (err, hash) {
                if (!err) {
                  console.log('start waiting second approve', hash)
                  _this.transactionReceiptAsync_(
                    hash,
                    function () {
                      console.log('!!!!2')
                      _storeWeb3.web3Instance().eth.sendRawTransaction(_this.sendApproveDepositAtomic(_from, _toDepo, _countTokensToDeposit, 'deposit'), function (err, hash) {
                        if (!err) {
                          console.log('start waiting depo tx', hash)
                          _this.transactionReceiptAsync_(
                            hash,
                            function () {
                              console.log('!!!!3')
                              let _payload = [hash, 'deposit', tokensToDepositNormal, '0.000']
                              _store.dispatch('writeTransaction', _payload)
                              _store.dispatch('getNewBalance')
                            },
                            ''
                          )
                        }
                      })
                    },
                    ''
                  )
                }
              })
            },
            ''
          )
        }
      })
    },
    sendTX (_from, _to, _value, _data, _type, _gta, _eth) {
      let _count = this.$store.state.web3.web3Instance().eth.getTransactionCount(_from)
      let rawTransaction = {
        'from': _from,
        'nonce': this.$store.state.web3.web3Instance().toHex(_count),
        'gasPrice': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasPrice),
        'gasLimit': this.$store.state.web3.web3Instance().toHex(this.$store.state.web3.gasContractCall),
        'to': _to,
        'value': _value,
        'data': _data
      }
      let privateKey
      if (this.$store.state.web3.private_input.indexOf('0x', 0) >= 0) {
        privateKey = this.$store.state.web3.private_input.substring(2)
      } else {
        privateKey = this.$store.state.web3.private_input
      }
      let privKey = Buffer.from(privateKey, 'hex')
      let tx = new Tx(rawTransaction)
      tx.sign(privKey)
      let serializedTx = tx.serialize()
      let _payload
      try {
        let _res = this.$store.state.web3.web3Instance().eth.sendRawTransaction('0x' + serializedTx.toString('hex'))
        if ((_type !== undefined) && (_type !== '')) {
          console.log('_res', _res)
          _payload = [_res, _type, _gta, _eth]
          this.$store.dispatch('writeTransaction', _payload)
        }
      } catch (err) {
        if ((_type !== undefined) && (_type !== '')) {
          _payload = [err.message, _type, '', '']
          this.$store.dispatch('writeTransaction', _payload)
        }
      }
    },
    clickAccountDeposite (event) {
      console.log('BIN address =', this.$store.state.GTAcontractInstance().address, ' from ', this.coinbase, 'GTAtoECBallowance=', this.GTAtoECBallowance, 'this.GTAbalanceNice=', this.GTAbalanceNice + 0, 'this.valueToDepo', this.valueToDepo)
      if (this.valueToDepo === 0) { console.log('error this.valueToDepo === 0'); return }
      if (this.GTAbalanceNice < this.valueToDepo) { console.log('error this.BINbalanceNice < this.valueToDepo'); return }
      if (this.GTAtoECBallowance < this.valueToDepo) {
        console.log('depo sendApproveDeposit')
        this.sendApproveDeposit(
          this.coinbase,
          this.$store.state.GTAcontractInstance().address,
          this.$store.state.ECBcontractInstance().address,
          this.valueToDepo * 10 ** 18,
          this.valueToDepo
        )
      } else {
        console.log('depo sendTX')
        this.sendTX(
          this.coinbase,
          this.$store.state.ECBcontractInstance().address,
          '0x0',
          this.$store.state.ECBcontractInstance().depositToken.getData(this.valueToDepo * 10 ** 18, { from: this.coinbase }),
          'deposite',
          this.valueToDepo,
          '0.000',
          function () { console.log('depo') }
        )
      }
      this.$store.dispatch('getNewBalance')
    },
    clickAccountWithdraw (event) {
      if (this.valueToWithdraw === 0) {
        console.log('this.valueToWithdraw === 0')
        return
      }
      if (this.valueToWithdraw > this.ECBcontractGTAbalanceNice) {
        console.log('this.valueToWithdraw > ECBcontractGTAbalanceNice')
        return
      }
      this.sendTX(
        this.coinbase,
        this.$store.state.ECBcontractInstance().address,
        '0x0',
        this.$store.state.ECBcontractInstance().withdrawToken.getData(this.valueToWithdraw * 10 ** 18, { from: this.coinbase }),
        'withdraw',
        this.valueToWithdraw,
        '0.000'
      )
      this.$store.dispatch('getNewBalance')
    }
  }
}
</script>

<style scoped>
.main {
  max-height: 300px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
input {  width: 300px;}
</style>



