<template>
  <v-app light>
  <v-toolbar app color="brown lighten-3">
   <v-toolbar-title style="font-weight: 150; font-size: 0.9em;"> <h1>wingo</h1></v-toolbar-title> <!-- Wesly Windot dewin-->
   <v-spacer></v-spacer>
      <v-menu offset-y close-on-click close-on-content-click>
        <v-btn block small flat color="brown lighten-3 px-0 mx-0" slot="activator"> <!--cyan lighten-5 -->
          <v-chip small style="font-size: 0.5em;">{{network + ' ' + GTABalancetoshow+' BIN '+ethBalancetoshow+' Eth '+ coinbasetoshow}}</v-chip >
        </v-btn>
        <v-list >
          <v-list-tile v-for="item of AccountsList /* storedAccounts */" v-bind:key="item.account" v-on:click="clickAccountItem"><v-list-tile-content style="font-size: 0.7em;">{{item.account}}</v-list-tile-content></v-list-tile>
          <new-Account-Dialog ref="ref_newAccDLG"></new-Account-Dialog>
          <v-list-tile v-on:click="clickNothing" @click.native.stop="runNewAccountDialog"><v-list-tile-content style="font-size: 0.7em;">New Account</v-list-tile-content></v-list-tile>
          <import-Account-Dialog ref="ref_importAccDLG"></import-Account-Dialog>
          <v-list-tile v-on:click="clickNothing" @click.native.stop="runAccountDialog"><v-list-tile-content style="font-size: 0.7em;">Import Account</v-list-tile-content></v-list-tile>
          <forget-Account-Dialog ref="ref_forgetAccDLG"></forget-Account-Dialog>
          <v-list-tile v-on:click="clickNothing" @click.native.stop="runForgetAccountDialog"><v-list-tile-content style="font-size: 0.7em;">Forget Account</v-list-tile-content></v-list-tile>
        </v-list>
      </v-menu>
 </v-toolbar>
 <v-content >
   <v-container fluid mx-0 my-0 px-0 py-0>
     <v-card flat color="brown">
       <acc-depo/>
       <v-card-text class="px-1 py-1">
         <v-layout row align-content-center class="horiz-scroll">
           <v-flex
                v-for="(item, index) in pairtfitems" :key="item.complex" @click="toggle(index)"
              >
              <v-list-tile-content v-if="selected.indexOf(index) < 0" class="grey--text">
                <v-list-tile-title class="text-xs-left" style="font-size: 0.8em;">{{item.pairtf}}</v-list-tile-title>
                <v-list-tile-sub-title class="grey--text" style="font-size: 0.5em;">{{item.state}}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-content font-weight-bold v-else class="white--text">
                <v-list-tile-title class="text-xs-left white--text" style="font-size: 0.8em;">{{item.pairtf}}</v-list-tile-title>
                <v-list-tile-sub-title class="white--text" style="font-size: 0.5em;">{{item.state}}</v-list-tile-sub-title>
              </v-list-tile-content>
          </v-flex>
        </v-layout>
       </v-card-text>
     </v-card>
     <tick-Chart/>
     <betting-component/>
     <result-grid/>
     <div v-if="ConnState != 'connected'" class="betclosed">
       <div class="loader">
         <div class="loader__bar"></div>
         <div class="loader__bar"></div>
         <div class="loader__bar"></div>
         <div class="loader__bar"></div>
         <div class="loader__bar"></div>
         <div class="loader__ball"></div>
       </div>
       <span>Connecting to blockchain. Please wait.</span>
     </div>
   </v-container>
 </v-content>
 <v-footer app color="brown darken-4" class="pa-3 grey--text">
   <v-spacer></v-spacer>
   <div class="hidden-lg-and-up" style="font-size: 0.6em;"><h5>&copy; {{ new Date().getFullYear() }} - ethercall.io</h5></div>
 </v-footer>
 </v-app>
<!--</div>-->
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
import AccDepo from '@/components/AccDepoMobile'
// import Betting from '@/components/BettingMobile'
import Betting from '@/components/BettingMobileMultiple'
import Sample from '@/components/sample'
import HelloMetamask from '@/components/hello-metamask'
import importAccDLG from '@/components/importAccDLG'
import forgetAccDLG from '@/components/forgetAccDLG'
import newAccDLG from '@/components/newAccDLG'
import resultGrid from '@/components/resultGridMobile'
import tickChart from '@/components/tickChartMobile'

export default {
  data: () => ({
    input_account: null,
    input_private: null,
    dialog: false,
    storedAccounts: [] // [{account: 'qweqweqweqweqweqwe', account_index: '1'}, {account: 'qweqweqweqweqweqweqwe2', account_index: '2'}]
  }),
  mounted () {
    console.log('forgetAccDLG.data.forgetAccountDialog', forgetAccDLG.data.forgetAccountDialog)
    console.log('dispatching getGTAContractInstance')
    this.$store.dispatch('getGTAContractInstance')
    console.log('dispatching getECBContractInstance')
    this.$store.dispatch('getECBContractInstance')
    console.log('dispatching getOTContractInstance')
    this.$store.dispatch('getOTContractInstance')

    var accountKeysStr2 = localStorage.getItem('ethercall2')
    if (accountKeysStr2 !== null) {
      var accountKeys2 = JSON.parse(accountKeysStr2)
      if (accountKeys2.accounts.length > 0) {
        console.log('setAccountsList', accountKeys2.accounts)
        this.$store.dispatch('setAccountsList', accountKeys2.accounts)
      }
      for (var i = 0; i < accountKeys2.accounts.length; i++) {
        console.log('1AccountStore.accounts[i].adr', accountKeys2.accounts[i].adr)
        this.storedAccounts.push({account: accountKeys2.accounts[i].adr, account_index: accountKeys2.accounts[i].prv})
      }
    }
  },
  computed: mapState({
    ConnState: state => state.web3.BlockchainConnState,
    pairtfitems: state => state.web3.keyPairTimeFrameList,
    selected: state => state.web3.keyPairTimeFrameListSelected,
    isInjected: state => state.web3.isInjected,
    network: state => NETWORKS[state.web3.networkId],
    coinbase: state => state.web3.coinbase,
    GTABalancetoshow: state => {
      if (state.web3.coinbaseselected) {
        let _GTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.GTAbalance, 'ether')
        let _dotposition = _GTAbalanceEther.indexOf('.')
        if (_dotposition > 0) {
          return _GTAbalanceEther.substring(0, _dotposition)
        } else {
          return _GTAbalanceEther
        }
      } else {
        return '0'
      }
    },
    coinbasetoshow: state => {
      if (state.web3.coinbaseselected) {
        let _acc = state.web3.coinbase
        return _acc.substring(0, 5) + '...' + _acc.substring(_acc.length - 3, _acc.length)
      } else {
        return 'Select account...'
      }
    },
    balance: state => state.web3.balance,
    ethBalancetoshow: state => {
      if (state.web3.web3Instance !== null) {
        if (state.web3.coinbaseselected) {
          return state.web3.web3Instance().fromWei(state.web3.balance, 'ether').substring(0, 7)
        } else {
          return '0.000'
        }
      }
    },
    // account_input: state => state.web3.account_input,
    private_input: state => state.web3.private_input,
    AccountsList: state => state.web3.AccountsList
  }),
  name: 'ethercall',
  beforeCreate () {
    console.log('registerWeb3 Action dispatched')
    this.$store.dispatch('registerWeb3')
  },
  components: {
    'hello-metamask': HelloMetamask,
    'import-Account-Dialog': importAccDLG,
    'forget-Account-Dialog': forgetAccDLG,
    'new-Account-Dialog': newAccDLG,
    'sample': Sample,
    'acc-depo': AccDepo,
    'betting-component': Betting,
    'result-grid': resultGrid,
    'tick-Chart': tickChart
  },
  methods: {
    toggle (index) {
      this.$store.dispatch('writekeyPairTimeFrameListSelected', [[index], this.pairtfitems[index].complex])
      this.$store.dispatch('setLastKnownBlockNumber', 0)
      this.$store.dispatch('getNewBalance')
      console.log('keyPairTimeFrameListSelectedcomplex', this.pairtfitems[index].complex)
    },
    // setAccount
    runAccountDialog () {
      // importAccDLG.data.accountdialog = true
      // console.log('importAccDLG.data.accountdialog = true', forgetAccDLG.data.forgetAccountDialog)
      var vm = this
      console.log('vm.$refs = ', vm.$refs) // .open()
      vm.$refs.ref_importAccDLG.open()
    },
    runForgetAccountDialog () {
      var vm = this
      vm.$refs.ref_forgetAccDLG.open()
    },
    runNewAccountDialog () {
      var vm = this
      vm.$refs.ref_newAccDLG.open()
    },
    clickNothing (event) { },
    clickAccountItem (event) {
      console.log('click!', event.target.innerText, event)
      // Find record and set it primary
      var AccountStore = JSON.parse(localStorage.getItem('ethercall2'))
      var foundIndex = -1
      var AccountAddress = event.target.innerText
      var AccountPrivate = -1
      if (AccountStore !== null) {
        // There is account store
        for (var i = 0; i < AccountStore.accounts.length; i++) {
          if (AccountStore.accounts[i].adr === AccountAddress) {
            foundIndex = i
            AccountPrivate = AccountStore.accounts[i].prv
            break
          }
        }
        if (foundIndex === -1) {
          console.log('Can"t find account. Something wrong....')
        } else {
          console.log('foundIndex', foundIndex)
          // Set coinbase account
          AccountStore.selectedAccount = foundIndex
          localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
          this.$store.dispatch('setWeb3AccountPrivate', {AccountAddress, AccountPrivate})
        }
      } else {
        console.log('Can"t find local store. Something wrong....')
        // TODO: Make unified functions to save, update, delete accounts from the list and update page
      }
    },
    clickAccountInput (event) {
      var AccountStore = JSON.parse(localStorage.getItem('ethercall2'))
      var newaccountdata = {
        adr: this.input_account,
        prv: this.input_private
      }
      var foundIndex = -1
      if (AccountStore !== null) {
        // There is account store
        for (var i = 0; i < AccountStore.accounts.length; i++) {
          console.log('AccountStore.accounts[i].adr', AccountStore.accounts[i].adr)
          if (AccountStore.accounts[i].adr === newaccountdata.adr) {
            foundIndex = i
            break
          }
        }
        if (foundIndex === -1) {
          AccountStore.accounts.push(newaccountdata)
          foundIndex = AccountStore.accounts.length - 1
        }
        console.log('findIndex', foundIndex)
        // AccountStore = {'accounts': [newaccountdata], selectedAccount: foundIndex}
        if (AccountStore.accounts[foundIndex].prv !== newaccountdata.prv) {
          AccountStore.accounts[foundIndex].prv = newaccountdata.prv
        }
        AccountStore.selectedAccount = foundIndex
        localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
        // Update list to the app store
        this.$store.dispatch('setAccountsList', AccountStore.accounts)
      } else {
        // There is no account store
        console.log('empty storage, filling...')
        foundIndex = 0
        console.log('findIndex')
        AccountStore = {'accounts': [newaccountdata], selectedAccount: foundIndex}
        localStorage.setItem('ethercall2', JSON.stringify(AccountStore))
        // Update list to the app store
        this.$store.dispatch('setAccountsList', newaccountdata)
      }
      this.input_account = ''
      this.input_private = ''
      this.dialog = false// close();
    }
  }
}
</script>

<style lang="scss">
  @font-face {
    font-family: "hemi-head-426-bold-italic";
    src: url('../assets/fonts/hemi-head-426-bold-italic.ttf');
  };
  * {
    //font-family: 'Open-Sans', sans-serif;
    // font-family: 'OpenSans-SemiBold';
    //font-family: "sans-serif";
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    //font-style: bold-italic;
  };
  h1 {
    font-family: "hemi-head-426-bold-italic";
    font-weight: 200;
    font-size: 2.5em;
  };
  h5 {
    font-family: "hemi-head-426-bold-italic";
    font-weight: 200;
    font-size: 1.0em;
  };
  h6 {
    font-family: "hemi-head-426-bold-italic";
    font-weight: 200;
    font-size: 1.3em;
  };
  .v-content__wrap{
    background: #4E342E;
  }
  .betclosed {
    position: absolute;
    background-color: rgba(94, 94, 94, 0.8);
    font-size: x-large;
    color: white;
    top:0;
    left:0;
    width:100%;
    height:100%;
    text-align: center;
  }
  .betclosed span {
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%, -50%);
    transform:translate(-50%, -50%);
    text-align: center;
  }
  // spash
  // Variables
$bar-color: #fff;
$ball-color: #fff;
$bg-color: #1A4961;

/* html {
  height: 100%;
}
body {
  height: 100%;
  background: $bg-color;
  display: flex;
  align-items: center;
  justify-content: center;
} */

.loader {
  top: 100px;
  left: calc(100%/2 - 36px);
  position: relative;
  width: 75px;
  height: 100px;

  &__bar {
    position: absolute;
    bottom: 0;
    width: 10px;
    height: 50%;
    background: $bar-color;
    transform-origin: center bottom;
    box-shadow: 1px 1px 0 rgba(0,0,0,.2);

    @for $i from 1 through 5 {
       &:nth-child(#{$i}) {
         left: ($i - 1) * 15px;
         transform: scale(1,$i*.2);
         animation: barUp#{$i} 4s infinite;
        }
    }
  }

  &__ball {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 10px;
    height: 10px;
    background: $ball-color;
    border-radius: 50%;
    animation: ball 4s infinite;
  }
  }
  @keyframes ball {
    0% {
      transform: translate(0, 0);
    }
    5% {
      transform: translate(8px, -14px);
    }
    10% {
      transform: translate(15px, -10px)
    }
    17% {
      transform: translate(23px, -24px)
    }
    20% {
      transform: translate(30px, -20px)
    }
    27% {
      transform: translate(38px, -34px)
    }
    30% {
      transform: translate(45px, -30px)
    }
    37% {
      transform: translate(53px, -44px)
    }
    40% {
      transform: translate(60px, -40px)
    }
    50% {
      transform: translate(60px, 0)
    }
    57% {
      transform: translate(53px, -14px)
    }
    60% {
      transform: translate(45px, -10px)
    }
    67% {
      transform: translate(37px, -24px)
    }
    70% {
      transform: translate(30px, -20px)
    }
    77% {
      transform: translate(22px, -34px)
    }
    80% {
      transform: translate(15px, -30px)
    }
    87% {
      transform: translate(7px, -44px)
    }
    90% {
      transform: translate(0, -40px)
    }
    100% {
      transform: translate(0, 0);
    }
  }
  @keyframes barUp1 {
    0% {
      transform: scale(1, .2);
    }
    40%{
      transform: scale(1, .2);
    }
    50% {
      transform: scale(1, 1);
    }
    90% {
      transform: scale(1,1);
    }
    100% {
      transform: scale(1,.2);
    }
  }
  @keyframes barUp2 {
    0% {
      transform: scale(1, .4);
    }
    40% {
      transform: scale(1, .4);
    }
    50% {
      transform: scale(1, .8);
    }
    90% {
      transform: scale(1, .8);
    }
    100% {
      transform: scale(1, .4);
    }
  }
  @keyframes barUp3 {
    0% {
      transform: scale(1, .6);
    }
    100% {
      transform: scale(1, .6);
    }
  }
  @keyframes barUp4 {
    0% {
      transform: scale(1, .8);
    }
    40% {
      transform: scale(1, .8);
    }
    50% {
      transform: scale(1, .4);
    }
    90% {
      transform: scale(1, .4);
    }
    100% {
      transform: scale(1, .8);
    }
  }
  @keyframes barUp5 {
    0% {
      transform: scale(1, 1);
    }
    40% {
      transform: scale(1, 1);
    }
    50% {
      transform: scale(1, .2);
    }
    90% {
      transform: scale(1, .2);
    }
    100% {
      transform: scale(1, 1);
    }
  }

  // splash
</style>



