<template>
  <div class='main'>
    <v-card flat color='brown darken-4'>
      <v-card-text class="py-0 px-2">
        <v-layout row class="white--text text--darken-1" style="font-size: 0.6em;"><b>Your transactions and stakes</b></v-layout>
      </v-card-text>
    </v-card>
    <v-tabs height=30 centered color="brown darken-2" dark>
    <v-tabs-slider color='deep-orange accent-4'></v-tabs-slider>
    <v-tab href="#tab-1" style="font-size: 0.6em;">Transactions</v-tab>
    <v-tab href="#tab-2" style="font-size: 0.6em;">Stakes</v-tab>
    <v-tab-item id="tab-1">
      <v-card flat color="brown darken-3">
        <v-list dark style="overflow-y:auto;">
          <v-list-tile v-for="tx in transactions" style="padding-left: 0px;" :key="tx.index">
            <v-list-tile-content style="padding-left: 0px;">
              <v-list-tile-title dark>
                <p style="font-size: 0.6em;">
                  {{ tx.txType + " " + tx.GtaValue + " BIN"}}
                </p>
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider inset></v-divider>
        </v-list>
      </v-card>
    </v-tab-item>
    <v-tab-item id="tab-2">
      <v-card flat color='brown darken-3'>
        <v-list subheader dark style="overflow-y:auto;"> <!-- style="overflow-y:auto" -->
          <v-list-tile v-for="stake in stakes" style="padding-left: 0px;" :key="stake.index">
            <v-list-tile-content style="padding-left: 0px;">
              <v-list-tile-title dark>
                <p style="font-size: 0.6em;">
                  {{ stake.pair + " " + stake.tf + " " + stake.bartime}}
                </p>
              </v-list-tile-title>
              <v-list-tile-sub-title dark v-if='stake.resCode == "win"'>
                  <p style="font-size: 0.7em; color:rgb(105, 240, 174);">
                    {{"staked " + stake.stake + " and " + stake.resCode + " value " + stake.value}}
                  </p>
              </v-list-tile-sub-title>
              <v-list-tile-sub-title dark v-if='stake.resCode == "loos"'>
                  <p style="font-size: 0.7em; color:rgb(255, 82, 82);">
                    {{"staked " + stake.stake + " and " + stake.resCode + " value " + stake.value}}
                  </p>
              </v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action style="font-size: 1em;">
              <v-list-tile-title>
                  <v-icon v-if='stake.type == "call"' color='green'>arrow_upward</v-icon>
                  <v-icon v-if='stake.type == "put"' color='red'>arrow_downward</v-icon>
              </v-list-tile-title>
            </v-list-tile-action>
          </v-list-tile>
          <v-divider inset></v-divider>
        </v-list>
      </v-card>
    </v-tab-item>
  </v-tabs>
  </div>
</template>

<script>
import {NETWORKS} from '../util/constants/networks'
import {mapState} from 'vuex'
export default {
  data () {
    return {
      pagination: {},
      input_account: null,
      new_balance: null,
      valueToDepo: 0,
      valueToWithdraw: 0,
      txHeaders: [
        { text: 'Transaction hash', value: 'txHash', align: 'center', sortable: false, width: '10%' },
        { text: 'Type', value: 'txType', align: 'center', sortable: false, width: '10%' },
        { text: 'BIN', value: 'GtaValue', align: 'center', sortable: false, width: '10%' },
        { text: 'ETH', value: 'EthValue', align: 'center', sortable: false, width: '10%' }
      ],
      stakesHeaders: [
        { text: 'Pair', align: 'center', sortable: false, value: 'pair', width: '10%' },
        { text: 'Time Frame', align: 'center', sortable: false, value: 'tf', width: '10%' },
        { text: 'Bar time', align: 'center', sortable: false, value: 'bartime', width: '10%' },
        { text: 'Type', value: 'type', align: 'center', sortable: false, width: '10%' },
        { text: 'Stake', value: 'stake', align: 'center', sortable: false, width: '10%' },
        { text: 'Result', value: 'result', align: 'center', sortable: false, width: '10%' },
        { text: 'Result value', value: 'resultValue', align: 'center', sortable: false, width: '40%' }
      ]
    }
  },
  computed:
    mapState({
      /* pages: state => {
        console.log('state.web3.TransactionsList', state.web3.TransactionsList.length)
        if (state.web3.TransactionsList.length == null || state.web3.TransactionsList.length === 0) { return 1 }
        return Math.ceil(state.web3.TransactionsList.length / 5)
      }, */
      isInjected: state => state.web3.isInjected,
      network: state => NETWORKS[state.web3.networkId],
      coinbase: state => state.web3.coinbase,
      transactions: state => state.web3.TransactionsList,
      stakes: state => state.web3.Awards,
      GTAbalance: state => state.web3.GTAbalance,
      GTAbalanceNice: state => {
        if (state.web3.web3Instance !== null) {
          let _GTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.GTAbalance, 'ether')
          let _dotposition = _GTAbalanceEther.indexOf('.')
          if (_dotposition > 0) {
            return parseInt(_GTAbalanceEther.substring(0, _dotposition))
          } else {
            return parseInt(_GTAbalanceEther)
          }
        }
      },
      ECBcontractGTAbalance: state => state.web3.BetGTAbalance,
      ECBcontractGTAbalanceNice: state => {
        if (state.web3.web3Instance !== null) {
          let _ECBcontractGTAbalanceEther = state.web3.web3Instance().fromWei(state.web3.BetGTAbalance, 'ether')
          let _dotposition = _ECBcontractGTAbalanceEther.indexOf('.')
          if (_dotposition > 0) {
            return parseInt(_ECBcontractGTAbalanceEther.substring(0, _dotposition))
          } else {
            return parseInt(_ECBcontractGTAbalanceEther)
          }
        }
      },
      GTAtoECBallowance: state => state.web3.GTAtoECBallowance
    }),
  methods: {
    changeClass (value) {
      return {
        'tds text-xs-center green darken-4': value === 'win',
        'tds text-xs-center brown darken-3': value === 'loos'
      }
    }
  }
}
</script>

<style scoped>
/* .main {  max-height: 300px;} */
input {  width: 300px;}
.smallfont {font-size: 0.6em;}
</style>



