import Vue from 'vue'
import Router from 'vue-router'
import ethercallDapp from '@/components/ethercall-dapp'
import ethercallDappMobile from '@/components/ethercallDappMobile'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/mobile',
      name: 'ethercall-mobile',
      component: ethercallDappMobile
    },
    {
      path: '/',
      name: 'ethercall-dapp',
      component: ethercallDapp
    }
  ]
})



